@import url(https://fonts.googleapis.com/css2?family=Public+Sans:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
.layout-header {
    position: fixed;
    top: 0;
    background: #2d5bd0;
    height: 5rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-bottom: 1px solid black;
}

.layout-header__bar{
    padding: .8rem 1rem;
    display: flex;
    justify-content: space-between;
}

.layout-logo{
    cursor: pointer;
    height: 3rem;
    font-family: 'Oswald', sans-serif;
    font-size: 38px;
    color: white;
}

.layout-header__navbar{
    width: 25rem;
    height: 100%;
    font-family: 'Public Sans', sans-serif;
    font-size: 24px;
    display: flex;
    justify-content: space-between;
    padding: .7rem 0;
    margin:0 2rem;
}

.layout-header__navbar-item{
    cursor: pointer;
    color: #ececec;
    height: 65%;
    border-bottom: 3px solid #fff;
    transition: .3s;
}

.layout-header__navbar-item:hover{
    color: rgb(39, 39, 39);
    border-bottom: 3px solid rgb(39, 39, 39);
}

.layout-header__navbar-movil{
    width: 4rem;
    padding: .4rem 0;
    height: 100%;
    display: none;
}

.layout-header__sideMenu-movil{
    width: 100%;
    height: calc(100vh - 5rem);
    background: #6D72CB;
    position: absolute;
    top: 5.05rem;
    animation-name: example;
    animation-duration: .5s;
}

@keyframes example {
    from {left: 100%;}
    to {left:0%;}
  }

@media screen and (max-width: 1000px) {
    .layout-header__navbar {
      display: none; /* The width is 100%, when the viewport is 800px or smaller */
    }

    .layout-header__navbar-movil{
        display: flex;
    }

    .layout-header__navbar-item{
        width: 12rem;
        height: 2.5rem;
        font-family: 'Public Sans', sans-serif;
        padding: .7rem 0;
        margin:0 2rem;
        font-size: 34px;
    }

    .layout-logo{
        margin: .5rem 0;
        font-size: 30px;
    }

    


  }




.home__main{
    width: 95%;
    margin: 1rem auto;
    padding: 7rem 0 0 0;
    height: calc(100vh - 6rem);
}


.home__tittle{
    font-size: 34px;
    width: 8.5rem;
    padding: 0 0 1rem 0;
    text-transform: uppercase;
    font-family: 'Public Sans', sans-serif;
}

.home__body{
    padding: 1rem;
    margin: 0 0 5rem 0;
    display: flex;
    flex-wrap: wrap;
}

.user-target__box{
    display: flex;
    flex-direction: column;
    cursor: pointer;
    width: 20rem;
    margin: .7rem;
    height: 25rem;
    border: 1px solid #a3a3a3;
    background: rgb(245, 245, 245);
    border-radius: 3px;
    transition: .2s;
}

.user-target__box:hover{ 
    box-shadow: 0px 0px 22px -11px rgba(0,0,0,0.52);
}

.user-target__content{
    font-family: 'Public Sans', sans-serif;
    display: flex;
    flex-direction: column;
    margin: 0 1rem;
}

.user-target__picture{
    width: 100%;
    height: 16rem;
    display: flex;
    justify-content: center;
}

.user-logo{
    width: 12rem;
    height: 12rem;
    margin-top: 2rem;
}

.user-target__body{
    display: flex;
    flex-direction: column;
}

.user-target__title{
    margin-left: 1rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.user-target__name{
    font-size: 26px;
    font-family: 'Segoe UI', sans-serif;
}

.user-target__mail{
    font-size: 18px;
    font-family: 'Segoe UI', sans-serif;
}

.user-target__phone{
    margin: 1rem 0 0 0;
    font-size: 17px;
    color: rgb(99, 99, 99);
    font-family: 'Segoe UI', sans-serif;
}

.user-target__web{
    font-size: 17px;
    color: rgb(99, 99, 99);
    font-family: 'Segoe UI', sans-serif;
}

.user-target__icon{
    color: rgb(44, 44, 44);
    margin: 0 .5rem 0 0;
}
.user-todo__box{
    display: flex;
    flex-direction: column;
    width: 20rem;
    margin: 0 .7rem;
    border: 1px solid #a3a3a3;
    background: rgb(249, 249, 249);
    border-radius: 3px;
    transition: .2s;
}


.user-todo__title{
    display: flex;
    align-items: center;
    width: 100%;
    height: 3rem;
    margin-left: 1rem;
    font-family: 'Public Sans', sans-serif;
    font-size: 22px;
}

.user-todo__content{
    width: 100%;
    height: 100%;
}

.user-todo__checkbox{
    font-family: 'Public Sans', sans-serif;
    display: flex;
    margin-left: 2rem;
    width: 17rem;
}

.user-todo__checkbox-text{
    cursor: pointer;
    width: 97%;
    height: auto;
    overflow-wrap: break-word;
}

.user-todo__checkbox-text__completed{
    cursor: pointer;
    text-decoration: line-through;
    color: #6E6E6E;
}

.user-post__box{
    display: flex;
    cursor: pointer;
    width: 95%;
    margin: .7rem .7rem 1.2rem .7rem;
    border: 1px solid #a3a3a3;
    background: rgb(249, 249, 249);
    border-radius: 3px;;
}

.user-post__image{
    width: 6rem;
    display: flex;
    justify-content: center;
}

.posts-user-logo{
    margin-top: 1.1rem;
    width: 3.9rem;
    height: 3.9rem;
}

.user-post__body{
    width: 88%;
}

.user-post__title{
    margin-top: 1.1rem;
    font-family: 'Public Sans', sans-serif;
    font-size: 22px;
    text-transform: uppercase;
}

.user-post__text{
    margin-bottom: 1rem;
    font-family: 'Public Sans', sans-serif;
    font-size: 18px;
    color: rgb(68, 68, 68);
    margin-bottom: 3rem;
}
.user-gallery__box{
    display: flex;
    flex-direction: column;
    width: 95%;
    margin: .7rem .7rem 1.2rem .7rem;
    border: 1px solid #a3a3a3;
    background: rgb(249, 249, 249);
    border-radius: 3px;;
}

.user-gallery__header{
    width: 100%;
    height: 8rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.user-gallery__title{
    font-family: 'Public Sans', sans-serif;
    font-size: 33px;
    margin: 1rem 1rem;
    text-transform: uppercase;
}

.user-gallery__input{
    font-family: 'Public Sans', sans-serif;
    font-size: 15px;
    padding-left: .3rem;
    height: 2rem;
    margin: 0 5rem 1rem 1rem;
    border-radius: 6px;
}

.user-gallery__body{
    width: 100%;
    height: 100%;
    margin: 0 1rem;
    display: flex;
    flex-wrap: wrap;

}

.user-gallery__photo{
    cursor: pointer;
    width: 6rem;
    margin: .2rem;
}

.modal-post__body{
    width: 100%;
    height: 100%;
}


.modal-post__main-answer{
    height: 9rem;
    display: flex;
    position: -webkit-sticky;
    position: sticky;
    top:0;
    background-color: #8ec4ff;
}

.modal-post__main-answer_logo{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 12rem;
    height: 100%;
}

.modal-post__main-answer_content{
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
}

.modal-post__main-answer_title{
    font-family: 'Public Sans', sans-serif;
    font-size: 22px;
    margin-top: 2rem;
    text-transform: uppercase;
}

.modal-post__main-answer_text{
    font-family: 'Public Sans', sans-serif;
    font-size: 16px;
}



.modal-post__user-logo{
    width: 6rem;
    height: 6rem;
}

.modal-post__comments{
    margin-top: 3rem;
   
}

.answer-comment__body{
    display: flex;
    justify-content: flex-end;
    width: 100%;
    height: auto;
    margin-top: 3rem;
}

.answer-comment__text{
    text-align: left;
    display: flex;
    flex-direction: column;
    width: 30rem;
    background: #f0f2f5;
    border-radius: 10px;
    padding: .5rem;
}

.answer-comment__logo{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-right: 3rem;
}

.answer-comment__logoImage{
    width: 4rem;
    height: 4rem;
    margin-left: 1rem;
}

.answer-comment__title{
    font-family: 'Public Sans', sans-serif;
    font-size: 18px;
    text-transform: uppercase;
    font-weight: 500;
}

.answer-comment__answer{
    margin-top: .2rem;
    overflow-wrap: break-word;
    font-family: 'Public Sans', sans-serif;
    font-size: 14px;
    color: rgb(43, 43, 43);
}

.answer-comment__mail{
    text-align: right;
    margin-top: .2rem;
    font-family: 'Public Sans', sans-serif;
    font-size: 13px;
    color: rgb(111 111 111);

}

.modal-photo__box{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
}

.modal-photo__title{
    position: absolute;
    padding-top: .4rem;
    font-family: 'Public Sans', sans-serif;
    font-size: 19px;
    bottom: 0;
    background-color: rgba(56, 56, 56, 0.651);
    color: white;
    width: 100%;
    text-align: center;
    min-height: 2rem;
}


.user__main{
    width: 98%;
    margin: .8rem auto;
    padding: 5rem 0 0 0;
    display: flex;
    height: calc(100vh - 8rem);
}

.user-panel__left{
    width: 23rem;
    display: flex;
    flex-direction: column;
}

.user-panel__middle{
    width: 62rem;
    height: 100%;
}

.user-panel__right{
    width: 30rem;
    height: 100%;
}

.user-panel__profile{
    width: 100%;
    height: 27rem;
}

.user-panel__todo{
    width: 100%;
}
body {
    margin: 0;
}

.clear-space{
    width: 100%;
    height: 1rem;
    background-color: white;
}

.clear-space-gray{
    width: 95%;
    height: 1rem;
    background-color: #f9f9f9;
    
}
